import { Board } from '../models/board-model';
import { BoardView, CardView } from '../views/board-view';
import { GameManager, GameState } from '../core/game-manager';

export class BoardController {
    private _model: Board;
    private _view: BoardView;
    private _gm: GameManager;

    public constructor(model: Board, view: BoardView) {
        this._model = model;
        this._view = view;
        this._gm = new GameManager(this._model.player, this._model.oponent);

        // Bind handlers to view events
        view.setCardClickHandler(this.handleEvent.bind(this));
    }

    public renderBoard(): void {
        let cards = this._model.getPlayerCards();
        let displayCards: CardView[] = [];
        cards.forEach((card): void => {
            displayCards.push({
                points:card.points,
                type: card.type});
        })
        this._view.renderPlayerCards(displayCards);
        cards = this._model.getOponentCards();
        displayCards = [];
        cards.forEach((card): void => {
            displayCards.push({
                points:card.points,
                type: card.type});
        })
        this._view.renderOponentCards(displayCards);
        this._view.renderPlayerHealth(this._model.getPlayerHealth());
        this._view.renderOponentHealth(this._model.getOponentHealth());
        this._view.renderPlayerShield(this._model.getPlayerShield());
        this._view.renderOponentShield(this._model.getOponentShield());
    }

    public handleEvent(event: Event): void {
        event.stopPropagation();
        switch(event.type){
            case "click":
                this.clickHandler(event.currentTarget as HTMLElement);
                break;
            default:
                break;
        }
    }

    private clickHandler(element: HTMLElement): void{
        if (this._gm.getState() === GameState.playerTurn) {
            this._model.playPlayerCard(parseInt(element.id));
            this.renderBoard();
            this._gm.endPlayerTurn();
            // TODO: Start and wait for Animation
            this._gm.endPlayerAnimation();
            if (this._gm.getState() === GameState.gameOver) {
                this._gameover();
            } else {
                this._oponentTurn();
            }
        }
    }

    private _oponentTurn(): void {
        if (this._gm.getState() === GameState.oponentTurn) {
            // TODO: get card index from AI
            const cardIndex = 0;
            this._model.playOponentCard(cardIndex);
            this.renderBoard();
            this._gm.endOponentTurn();
            // TODO: Start and wait for Animation
            this._gm.endOponentAnimation();
        }
        if (this._gm.getState() === GameState.gameOver) {
            this._gameover();
        }
    }

    private _gameover(): void {
        console.log("GAME OVER!");
        this._newGame();
    }

    private _newGame(): void {
        console.log("Staring a new game.");
        this._model.newGame();
        this.renderBoard();
        this._gm.newGame(this._model.player, this._model.oponent);
    }
}
