import { Player } from './player';

export enum GameState {
    paused,
    playerTurn,
    oponentTurn,
    playerAnimation,
    oponentAnimation,
    gameOver
}

export class GameManager {
    private _gameState: GameState = GameState.playerTurn;
    private _player: Player;
    private _oponent: Player;

    public constructor(player: Player, oponent: Player) {
        this.newGame(player, oponent);
    }

    public getState(): GameState {
        return this._gameState;
    }

    public endPlayerTurn(): void {
        if ( this._gameState === GameState.playerTurn) {
            this._gameState = GameState.playerAnimation;
        }
    }

    public endOponentTurn(): void {
        if ( this._gameState === GameState.oponentTurn) {
            this._gameState = GameState.oponentAnimation;
        }
    }

    public endPlayerAnimation(): void {
        if (this._oponent.health > 0) {
            this._gameState = GameState.oponentTurn;
        } else {
            this._gameState = GameState.gameOver;
        }
    }

    public endOponentAnimation(): void {
        if (this._player.health > 0) {
            this._gameState = GameState.playerTurn;
        } else {
            this._gameState = GameState.gameOver;
        }
    }

    public newGame(player: Player, oponent: Player): void {
        this._player = player;
        this._oponent = oponent;
        this._gameState = GameState.playerTurn;
    }
}
